import axios from 'axios';
import _ from 'lodash'
axios.defaults.headers.common['X-Requested-With'] = 'XMLHttpRequest';

// axios.defaults.baseURL = 'http://localhost:3000'
// const apiBase = process.env.VUE_APP_API_BASE_URL;
// const apiBase = 'http://localhost:57386/api/'

// testAPIBase = 'http://tintweb02:1050/api/'
const apiBase = '/'

axios.interceptors.response.use(resp => {
    return resp;
}, err => {
    if (err && err.response.status == 401) {
        location.reload();
    }
    return Promise.reject(err);
});

function getAllDoc() {
    return axios.get(apiBase + 'documentsigning/getall').then(res => {
        return res.data.map(obj => {
            return _.transform(obj, function (result, val, key) {
                result[key[0].toLowerCase() + key.slice(1)] = val;
            });
        })

        // return res.data
    })
}

function getDocById(id) {
    return axios.get(apiBase + 'documentSigning/get/' + id)
}

function deleteDocById(id) {
    return axios.post(apiBase + 'DocumentSigning/Delete/' + id )
}

