<template>
    <div>
        <tile title="My Pay application configuration" icon="icon-wrench" class="mypay-config-tile w-100">
            <div class="section">
                <div class="section-body">
                    <div class="default-form">
                        <div class="form-row">
                            <label>Host URL:</label>
                            <input type="text"/>
                        </div>
                        <div class="form-row">
                            <label>Show Summary</label>
                            <input type="checkbox" v-model="config.ShowSummary"/>
                        </div>
                        <div class="form-row row-bottom">
                            <button class="button green" @click.prevent="saveConfig"><span
                                class="b-icon icon-save"></span><span>Save</span></button>
                        </div>
                    </div>
                </div>
            </div>
        </tile>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {
            config: {}
        }
    },
    mounted() {
        this.$root.loading = true;

        axios.get('/MyPay/GetConfig')
            .then(resp => {
                this.config = resp.data;
            })
            .catch(() => {
            })
            .then(() => {
                this.$root.loading = false;
            });
    },
    methods: {
        saveConfig() {
            this.$root.loading = true;

            axios.post('/MyPay/SaveConfig', this.config)
                .catch(() => {
                })
                .then(() => {
                    this.$root.loading = false;
                });
        }
    }
}
</script>
