import VueRouter from "vue-router";
import Dashboard from "@/dashboard";
import CleverForms from "@/apps/clever-forms";
import MyPay from "@/apps/mypay";
import AppTable from "@/app-table";
import HelloSign from "@/component/HelloSign/HelloSign";

const routes = [
    {
        path: '/Dashboard',
        component: Dashboard
    },
    {
        path: '/CleverForms',
        component: CleverForms
    },
    {
        path: '/MyPay',
        component: MyPay
    },
    {
        path: '/DocumentSignatures',
        component: HelloSign
    },
    {
        path: '*',
        component: AppTable
    }
]
// linkActiveClass: 'selected'
const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})
export default router