<template>
    <div>
        <tile title="Clever Forms application configuration" icon="icon-wrench" class="cf-config-tile w-100">
            <div class="section">
                <div class="section-body">
                    <div class="default-form">
                        <div class="form-row">
                            <label>Host URL:</label>
                            <input type="text" v-model="config.Url"/>
                        </div>
                        <hr/>
                        <div class="title-row section-title">Forms</div>
                        <div class="forms-config-box form-row">
                            <div class="form-list">
                                <ul>
                                    <li v-for="(form, index) of config.Forms"
                                        :class="{ selected: currentForm && currentForm.Name == form.Name }" :key="index"
                                        @click="selectForm(form)">
                                        <span :class="'icon icon-' + form.Icon"></span>
                                        <span>{{ form.Text }}</span>
                                        <span class="icon icon-cross btn-remove" @click.stop="deleteForm(form)"></span>
                                    </li>
                                    <li @click="addForm()">
                                        <span class="icon icon-plus"></span>
                                        <span><b>Add new form</b></span>
                                    </li>
                                </ul>
                            </div>
                            <div class="app-config" v-if="currentForm">
                                <div class="title-row section-title">Link</div>
                                <br/>
                                <div class="form-row">
                                    <label>Enabled:</label>
                                    <input type="checkbox" v-model="currentForm.Enabled"/>
                                </div>
                                <div class="form-row">
                                    <label>Name:</label>
                                    <input type="text" v-model="currentForm.Name"/>
                                </div>
                                <div class="form-row">
                                    <label>Link text:</label>
                                    <input type="text" v-model="currentForm.Text"/>
                                </div>
                                <div class="form-row">
                                    <label>Link icon:</label>
                                    <input type="text" v-model="currentForm.Icon"/>
                                </div>
                                <div class="form-row">
                                    <label>Template ID:</label>
                                    <input type="text" v-model="currentForm.TemplateId"/>
                                </div>
                            </div>
                        </div>
                        <div class="form-row row-bottom">
                            <button class="button green" @click.prevent="saveConfig"><span
                                class="b-icon icon-save"></span><span>Save</span></button>
                        </div>
                    </div>
                </div>
            </div>
        </tile>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    data() {
        return {
            config: {},
            currentForm: null
        }
    },
    mounted() {
        this.$root.loading = true;

        axios.get('/CleverForms/GetConfig')
            .then(resp => {
                this.config = resp.data;
            })
            .catch(() => {
            })
            .then(() => {
                this.$root.loading = false;
            });
    },
    watch: {
        config() {
            this.selectForm();
        }
    },
    methods: {
        selectForm(form) {
            this.currentForm = form ? form : this.config.Forms[0];
            if (this.currentForm && !this.currentForm.Link) this.currentForm.Link = {};
        },
        addForm() {
            let index = this.config.Forms.length + 1;

            let form = {
                Name: `Form${index}`,
                Text: `Form ${index}`,
                Icon: 'question'
            };

            this.config.Forms.push(form);
            this.selectForm(form);
        },
        deleteForm(form) {
            let index = this.config.Forms.indexOf(form);
            this.config.Forms.splice(index, 1);
            this.selectForm(this.config.Forms[index - 1]);
        },
        saveConfig() {
            this.$root.loading = true;

            axios.post('/CleverForms/SaveConfig', this.config)
                .catch(() => {
                })
                .then(() => {
                    this.$root.loading = false;
                });
        }
    }
}
</script>


