<template>
<div class="main-wrapper">
    <div class="heading">
        <h1>Document Signatures</h1>
    </div>

    <div v-if="isEditing || isCreating">
        <EditForm :selectedDocInput="selectedDoc" :isCreate="isCreating" :isEdit="isEditing" :helloSignDocuments="helloSignDocuments"
                  @cancelEdit="cancelEdit"
            @docSaved="docSaved"></EditForm>
    </div>

    <div class="display-table mt-4">
        <div class="table-grid">
            <div class="table-heading">
                <div class="heading-cell break-word">TemplateId</div>
                <div class="heading-cell">Description</div>
                <div class="heading-cell">Title</div>
                <div class="heading-cell">Enabled</div>
                <div class="heading-cell"></div>
            </div>
            <div class="table-body" v-for="doc in helloSignDocuments">
                <div class="body-cell">
                    {{doc.externalTemplateId}}
                </div>
                <div class="body-cell">{{doc.description}}</div>
                <div class="body-cell">{{doc.title}}</div>
                <div class="body-cell">{{doc.enabled}}</div>
                <div class="body-cell">
                    <div class="d-flex">
                        <button class="blue" @click="openEditDoc(doc)">Edit</button>
                        <button class="orange delete-button" @click="openDeleteDoc(doc)">Delete</button>
                    </div>

                </div>
            </div>
        </div>
    </div>
    <div class="d-flex justify-end mt-4">
        <button class="blue mr-4" @click="openCreateDoc">Create</button>
    </div>
    <modal ref="DeleteConfirmRef">
        <template v-slot:body>
            <h3>Are you sure you want to delete this template?</h3>
        </template>
        <template v-slot:footer>
            <div class="mt-4">
                <button class="button blue" @click="confirmDelete">Yes</button>
                <button class="button grey ml-4" @click="cancelDelete">No</button>
            </div>

        </template>
    </modal>

</div>
</template>

<script>

import Remote from '../../remote/remote'
import EditForm from "@/component/HelloSign/EditForm";
import _ from "lodash";
import Modal from "@/common/Modal";

export default {
    name: "HelloSign",
    components: {EditForm, Modal},
    data() {
        return {
            selectedDoc: null,
            targetDeleteDoc: null,
            helloSignDocuments: [],

            isEditing: false,
            isCreating: false,
            isDeleting: false,
        }
    },
    mounted() {
        this.loadHelloSign();
    },

    methods: {
        loadHelloSign() {
            Remote.getAllDoc()
                .then(res => {
                    console.log('res', res)
                    let filledRolesDocs = res.map(doc => {
                        doc.roles = _.times(4, (i) => {
                            if (doc.roles.length > i) {
                                return doc.roles[i]
                            } else {
                                return {
                                    Name: '',
                                    Order: i + 1
                                }
                            }
                        })
                        return doc
                    })

                    this.helloSignDocuments = filledRolesDocs
                })
        },

        openEditDoc(doc) {
            this.clearState();
            console.log('edit', doc)
            this.selectedDoc = doc
            this.isEditing = true
        },

        openCreateDoc() {
            this.clearState();
            this.selectedDoc = {}
            this.isCreating = true;
        },

        openDeleteDoc(doc) {
            this.clearState();
            this.targetDeleteDoc = doc;
            this.$refs.DeleteConfirmRef.toggleShow()
        },

        cancelDelete() {
            this.$refs.DeleteConfirmRef.toggleShow()
        },

        confirmDelete() {
            let doc = this.targetDeleteDoc
            Remote.deleteDocById(doc.id)
                .then(() => {
                    this.$refs.DeleteConfirmRef.toggleShow()
                    this.loadHelloSign();
                }, (err) => {
                    this.$refs.DeleteConfirmRef.toggleShow()
                    console.error(err)
                    alert('Delete Doc Failed')
                })
        },

        clearState() {
            this.isEditing = false;
            this.isCreating = false;
            this.isDeleting = false;
        },
        cancelEdit() {
            this.clearState();
        },
        docSaved() {
            this.loadHelloSign();
            this.clearState();
        }
    }
}
</script>

<style scoped lang="scss">
    .table-heading {
        display: grid;
        border-collapse: collapse;
        min-width: 100%;
        grid-template-columns: 20% 1fr 20% 20% 20%;
        border: 1px solid grey;
        grid-gap: 1px;
        background: grey;
    }

    .heading-cell {
        display: grid;
        justify-items: center;
        align-items: center;
        padding: 4px 10px;
        background: linear-gradient(to bottom, #fcfcfc 0%, #efefef 49%, #e2e2e2 51%, #d6d6d6 100%);
    }

    .table-body {
        display: grid;
        border-collapse: collapse;

        grid-template-columns: 20% 1fr 20% 20% 20%;

        border: 1px solid grey;
        border-top: 0;
        background: grey;
        grid-gap: 1px;

    }

    .body-cell {
        display: grid;
        background: white;
        justify-items: center;
        align-items: center;
        padding: 4px 10px;
        width: auto;
    }

    .break-word {
        word-break: break-all;
    }

    .newui button {

        .mt-4 {
            margin-top: 8px;
        }
    }

    .newui .delete-button {
        margin-left: 4px;
    }

    .main-wrapper {
        background: #f2f2f2;
        padding: 10px;
    }

    .newui {
        .mt-1 {
            margin-top: 4px;
        }

        .mt-4 {
            margin-top: 16px;
        }

        .ml-4 {
            margin-left: 16px;
        }

    }



</style>