import Vue from 'vue';
import VueRouter from 'vue-router';
import Tile from './tile.vue';
import AppTable from './app-table.vue';
import Dashboard from './dashboard.vue';
import exDashboard from '../external-lib/dashboard/index.vue'

import CleverForms from './apps/clever-forms.vue';
import MyPay from './apps/mypay.vue';
import Modal from './modal.vue';
import NavBar from './navbar.vue';
import axios from 'axios';
import VeeValidate from 'vee-validate';
import {
    Validator
} from 'vee-validate';

import remote from './remote/remote.js'
import common from './common/copied.js'
import router from "@/router";
import App from './index'

import './styles/ex-common/ui.default.less'
import './styles/ex-common/ui.font.icons.less'

import './styles/index.scss'

Vue.use(VueRouter);
Vue.use(VeeValidate);

export const reservedLinks = [
    '/Profile/Security',
    '/Profile/Approvals',
    '/Profile/PeopleStreme',
    '/CleverForms/Inbox',
    '/CleverForms/Template',
    '/CleverForms/Admin'
];

export const reservedNames = [
    'MyDocuments',
    'BankAccounts',
    'StaffSearch',
    'Leave',
    'MyPay',
    'MyAccount',
    'WorkSchedules',
    'CleverForms',
    'Timesheets'
];

Validator.localize('en', {
    messages: {
        url: () => 'Please enter the full URL',
        required: () => 'This field is required',
        numeric: () => 'The field may only contain numeric characters'
    }
});

Validator.extend('url', {
    validate(value) {
        var regex = /^(http|https):\/\/+[\www\d]+\.[\w]+(\/[\w\d]+)?/;
        return regex.test(value);
    }
});

Validator.extend('reservedLinks', {
    validate(value) {
        var path = new URL(value).pathname;
        return reservedLinks.includes(path) ? false : true;
    },
    getMessage: () => 'Sorry, you can\'t use that URL'
});

Validator.extend('reservedNames', {
    validate(value) {
        return reservedNames.includes(value) ? false : true;
    },
    getMessage: () => 'Sorry, you can\'t use that name'
});

Vue.component('tile', Tile);
Vue.component('modal', Modal);
Vue.component("dashboard", exDashboard);

new Vue({
    data() {
        return {
            loading: true,
            dashboardConfig: {},
            loader: window.config.loaders.dark,
            showNav: false,
            hideNav: false,
            rank: 0
        }
    },
    router,
    render: h => h(App),
}).$mount('#app')