<template>
    <div class="dashboard" :class="{ 'show-nav': showNav, 'hide-nav': hideNav }">
        <div class="dash-header">
            <div id="mobile-nav-toggle" class="dash-menu-toggle" @mousedown.stop="">
                <span class="icon-list" @click="onNavToggle(true)"></span>
            </div>
            <div id="desktop-nav-toggle" class="dash-menu-toggle">
                <span class="icon-list" @click="onNavToggle(false)"></span>
            </div>
            <!--        <div class="dash-header-title displayname">@ViewData["CompanyNo"]</div>-->
            <div class="dash-header-sub-title">Application Configuration</div>
            <div class="dash-header-logo">
                <img src="/Content/images/companylogos/affinity.png" />
            </div>
        </div>
        <div class="pagewrapper">
            <div class="UIPromtBoxBG" v-if="$root.loading">
                <div class="UIPromtBox loader">
                    <img :src="loader"><span class="content aligncenter">&nbsp;&nbsp;Please wait...</span>
                </div>
            </div>
            <nav-bar v-show="!$root.loading" @hide="onNavHide"></nav-bar>
            <div v-show="!$root.loading" class="pagecontent">
                <div class="contentwrapper">
                    <router-view></router-view>
                </div>
            </div>
        </div>
    </div>


<!--    <dashboard :enable-by-config="true"-->
<!--               config-url="/Config/Dashboard" info-url="/Auth/GetInfo" logout-url="/Auth/Logout">-->
<!--        <schedules-navigation></schedules-navigation>-->
<!--        <alert></alert>-->
<!--        <div>-->
<!--            <router-view></router-view>-->
<!--        </div>-->
<!---->
<!--    </dashboard>-->
<!--    <div id="app">-->
<!--        <router-view/>-->
<!--    </div>-->
</template>

<script>
import NavBar from './navbar.vue';
import axios from "axios";
import {reservedLinks, reservedNames} from "@/main";

export default {
    data() {
        return {
            loading: true,
            dashboardConfig: {},
            loader: window.config.loaders.dark,
            showNav: false,
            hideNav: false,
            rank: 0
        }
    },
    methods: {
        getDashboardConfig() {
            this.$root.loading = true;
            axios.get('/Manage/GetConfig')
                .then(resp => {
                    this.$root.dashboardConfig = resp.data;
                    this.$root.dashboardConfig.Apps.filter(app => {
                        app.Link.Menu.forEach(function (menu) {
                            console.log('menu', menu)
                            var show = true;
                            try {
                                var path = new URL(menu.Url).pathname;
                                show = reservedLinks.includes(path) ? false : true;
                            } catch (e) {
                                console.error('link process error: ', e)
                            }

                            menu.Show = show;
                        });
                        app.ShowName = reservedNames.includes(app.Name) ? false : true;
                        app.Valid = true;
                    });
                    this.rank = Math.max(...this.$root.dashboardConfig.Apps.map(app => app.Rank)) + 1;
                })
                .catch(() => {
                })
                .then(() => {
                    this.$root.loading = false;
                });
        },
        onNavToggle(mobile) {
            if (mobile) this.$root.showNav = !this.$root.showNav;
            else this.$root.hideNav = !this.$root.hideNav;
        },
        onNavHide() {
            this.$root.showNav = false;
        }
    },
    mounted() {
        this.getDashboardConfig();
        this.$root.getDashboardConfig = this.getDashboardConfig;
    },
    components: {
        NavBar
    },
}


</script>
