<template>
    <div class="edit-dialog mt-4" v-if="selectedDoc">
        <div>
            <h3>Create Document Signature</h3>
        </div>

        <div class="mt-4 red" v-if="errorMessages && errorMessages.length > 0">
            <span v-for="error in errorMessages">{{error}}</span>
        </div>
        <ul class="mt-4">
            <li v-for="(group, i) in errors.collect()">
                <ul>
                    <li class="red mt-1" v-for="error in group"> {{i}} : {{ error }}</li>
                </ul>
            </li>
        </ul>
        <div class="mt-4">
            <span class="mr-4 label-width" v-if="isEdit">Id</span>
            <span>{{selectedDoc.id}}</span>
        </div>
        <div class="mt-4">
            <span class="mr-4 label-width">Document Id</span>
            <span v-if="isEdit">{{selectedDoc.externalTemplateId}}</span>
            <input type="text" v-model="selectedDoc.externalTemplateId"
                   v-if="isCreate"
                   name="Document Id"
                   v-validate="'required'"
                   @change="clearError">
        </div>
        <div class="mt-2 d-flex">
            <span  class="mr-4 label-width">Description</span>
            <textarea rows="4" v-model="selectedDoc.description" name="Description"
                      v-validate="'required'">
            </textarea>
        </div>
        <div class="mt-2" v-for="n in 4">
            <span  class="mr-4 label-width">Role Title {{n}} <span v-if="n == 1">*</span></span>
            <input type="text" v-if="selectedDoc.roles && selectedDoc.roles[n - 1] && n === 1"
                   v-model="selectedDoc.roles[n - 1].Name"
                   name="Role1"
                   v-validate="'required'">
            <input type="text" v-if="selectedDoc.roles && selectedDoc.roles[n - 1] && n > 1"
                   v-model="selectedDoc.roles[n - 1].Name">
        </div>
        <div class="mt-2">
            <span class="mr-4 label-width">Title</span>
            <input type="text" v-model="selectedDoc.title" name="Title"
                   v-validate="'required'">
        </div>
        <div class="mt-2">
            <span class="mr-4 label-width">Email Subject</span>
            <input type="text" v-model="selectedDoc.emailSubject">
        </div>
        <div class="mt-2">
            <span class="mr-4 label-width">Email Body</span>
            <textarea v-model="selectedDoc.emailBody"></textarea>
        </div>
        <div class="mt-2">
            <span class="mr-4 label-width">Enabled</span>
            <input type="checkbox" v-model="selectedDoc.enabled">
        </div>
        <div class="mt-4">
            <button class="grey" @click="cancel">Cancel</button>
            <button class="blue ml-4" @click="save">Save</button>
        </div>
    </div>
</template>

<script>
import Remote from "@/remote/remote";
import _ from 'lodash'
import axios from "axios";

export default {
name: "EditForm",
    props: {
        selectedDocInput: Object,
        isCreate: Boolean,
        isEdit: Boolean,
        helloSignDocuments: Array
    },
    data() {
        return {
            selectedDoc: null,
            errorMessages: []
        }
    },
    mounted() {
        console.log('is create', this.isCreate)
        this.selectedDoc = _.clone(this.selectedDocInput)
        if (this.isCreate) {
            this.selectedDoc.roles = _.times(4, (i) => {
                return {
                    Name: '',
                    Order: i + 1
                }
            })
        }
    },
    watch: {
        selectedDocInput(value1, value2) {
            if (this.selectedDocInput) {
                this.selectedDoc = _.clone(this.selectedDocInput)

                if (this.isCreate) {
                    this.selectedDoc.roles = _.times(4, (i) => {
                        return {
                            Name: '',
                            Order: i + 1
                        }
                    })
                }
            }
        }
    },

    methods: {
        save() {
            this.$validator.validateAll()
                .then((result) => {
                    if (result) {
                        // eslint-disable-next-line
                        console.log('Form validated pass');
                        this.editOrCreate();
                        return;
                    }
                console.error('Correct them errors!');
            });
        },

        editOrCreate() {
            if (!this.isEdit) {
                this.errorMessages = [];
                // check duplicate when create new hello sign
                let findDuplicate = this.helloSignDocuments.find(doc => {
                    return doc.externalTemplateId === this.selectedDoc.externalTemplateId
                })

                if (findDuplicate !== undefined) {
                    // todo: add duplicate error
                    console.error('duplicated')
                    this.errorMessages.push('Template Id is duplicated')
                    return;

                }
            }

            let filteredRoles = this.selectedDoc.roles.filter(role => {
                return role.Name !== undefined && role.Name !== ''
            })

            let data = {
                Id: this.selectedDoc.id,
                Description: this.selectedDoc.description,
                ExternalTemplateId: this.selectedDoc.externalTemplateId,
                Roles: filteredRoles,
                Title: this.selectedDoc.title,
                EmailSubject: this.selectedDoc.emailSubject,
                EmailBody: this.selectedDoc.emailBody,
                Enabled: !!this.selectedDoc.enabled

            }

            Remote.saveDoc(data).then(res => {
                console.log('res', res)
                this.$emit('docSaved')
            })
        },

        cancel() {
            this.$emit('cancelEdit')
        },

        loadHelloSign() {

        },

        clearError() {
            this.errorMessages = [];
        }
    }
}
</script>

<style lang="scss" scoped>

$input-width: 230px;

.label-width {
    display: inline-block;
    width: 100px;
}

textarea {
    margin: 0;
    padding: 0 5px;
    border-top: 1px solid #b3b3b3;
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #ffffff;
    border-left: 1px solid #e6e6e6;
    font-size: 12px;
    font-family: Verdana, Tahoma, Arial, "Helvetica Neue", Helvetica, Sans-Serif;
    background: #ffffff;
    min-height: 25px;
}

input {
    box-sizing: border-box;
    border-top: 1px solid #b3b3b3;
    border-right: 1px solid #e6e6e6;
    border-bottom: 1px solid #fff;
    border-left: 1px solid #e6e6e6;
}

input:focus {
    border: 1px solid #44b5ec;
    box-shadow: 0 0 3px #44b5ec;
}

input[type=text] {
    width: $input-width;
}

textarea {
    width: $input-width;
    box-sizing: border-box;
}

.ml-4 {
    margin-left: 16px;
}
</style>