<template>
    <div id="dash-nav" class="dash-nav accordion" @mousedown="onMouseDown">
        <div class="top">
            <router-link class="dash-nav-item" :to="{ path: '/Dashboard' }">
                <span class="dash-nav-item-inner">
                    <span class="icon icon-gauge"></span>
                    <span class="label">Dashboard</span>
                </span>
            </router-link>
            <router-link v-for="(app, index) of visibleApps" class="dash-nav-item" :to="{ path: '/' + app.Name }"
                         :key="index">
                <span class="dash-nav-item-inner">
                    <span :class="'icon icon-' + app.Link.Icon"></span>
                    <span class="label">{{ app.Link.Text || app.Name }}</span>
                </span>
            </router-link>
            <router-link class="dash-nav-item" :to="{ path: '/DocumentSignatures' }">
                <span class="dash-nav-item-inner">
                    <span class="icon icon-docs"></span>
                    <span class="label">Document Signatures</span>
                </span>
            </router-link>
        </div>
        <div class="bottom">
            <a class="dash-nav-item" href="/Auth/Logout">
                <span class="dash-nav-item-inner">
                    <span class="icon icon-logout"></span>
                    <span class="label">Logout</span>
                </span>
            </a>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            apps: [],
            mousedown: false
        }
    },
    methods: {
        onMouseDown() {
            this.mousedown = true;
        },
        onBodyMouseDown(evt) {
            if (!this.mousedown) this.$emit('hide');
            this.mousedown = false;
        }
    },
    created() {
        this.$root.$watch('dashboardConfig', (config) => {
            this.apps = config.Apps;
        });
        document.body.addEventListener('mousedown', this.onBodyMouseDown);
    },
    beforeDestroy() {
        document.body.removeEventListener('mousedown', this.onBodyMouseDown);
    },
    computed: {
        visibleApps() {
            let visibleApps = this.apps;
            if (this.apps) {
                visibleApps = this.apps.filter(app => {
                    return app.Name != 'StaffSearch'
                })
            }
            return visibleApps;
        }
    }
}
</script>
